import { useEffect, useState } from 'react';
import { UserListApi } from '../../utils/adminApi';
import { Table } from 'antd';
const { Column } = Table;

const Record = () => {
  const [tableData, setTableData] = useState();

  useEffect(() => {
    UserListApi().then((res) => {
      setTableData(
        res.data.map((item) => ({
          ...item,
          _id: item._id.slice(0, 7),
          createdAt: item.createdAt.replace(/[TZ]/g, ' '),
          updatedAt: item.updatedAt.replace(/[TZ]/g, ' '),
        }))
      );
    });
  }, []);


  return (
    <Table dataSource={tableData} rowKey="_id">
      <Column title="ID" dataIndex="_id" />
      <Column title="机器人编号" dataIndex="nick" />
      <Column title="机器人名称" dataIndex="level" />
      <Column title="会员" dataIndex="phone" />
      <Column title="已发放收益" dataIndex="blance" />
      <Column title="收益/每小时" dataIndex="freeze" />
      <Column title="购买价格" dataIndex="freeze" />
      <Column title="购买时间" dataIndex="createdAt" />
      <Column title="运行时间" dataIndex="updatedAt" />
      <Column title="状态" dataIndex="state" />
    </Table>
  );
};

export default Record;
